body{
    background-color: white;
}
header{
    display: flex;
    flex-direction: row;
    justify-content: center;
    border-bottom: 1px solid rgba(243, 242, 242, 0.877);
    box-shadow: 0 0 15px rgb(216, 214, 214);
}
nav{
    padding: 18px 0 10px 13px;
    display: flex;
}
nav>a{
    margin-right: 55px;
    font-size: 17px;
    color: rgb(26, 25, 25);
    text-decoration: none;
}
.nav_and_search{
    width: 900px;
}
header>img{
    width: 150px;
    height: 150px;
    object-fit: cover;
}
a:hover{
    cursor: pointer;
}
main{
    display: flex;
    align-items: center;
    flex-direction: column;
}
main>div{
    display: flex;
    justify-content: center;
}
footer{
    display: flex;
    justify-content: center;
}
a{
    text-decoration: none;
    color: black;
}